.container{height:100vh;display:flex;justify-content:center;align-items:center;background-color:#f6f8fc}.card{display:flex;flex-direction:column;align-items:center;max-width:360px;width:100%;border:1px solid #e2e9f3;background-color:#fff;border-radius:6px;padding:24px 48px 32px}.card__title{font-size:1.71em;font-weight:500;line-height:1.7em;color:#2b3151}.card__subtitle-wrapper{display:flex;align-items:baseline;margin-bottom:8px}.card__subtitle{margin-top:8px;font-size:1.14em;line-height:1.37em;color:#2b3151}.card__sign-button{margin-left:4px;border-style:none;font-size:1.14em;background-color:#fff;color:#2856ae;transition:color 0.5s ease-out}.card__sign-button:hover{cursor:pointer;color:#00308d}.card__button-wrapper{display:flex;flex-direction:column}.card__button-microsoft,.card__button-google{display:flex;justify-content:space-between;align-items:center;position:relative;padding:0 40px 0 56px;margin-top:16px;width:100%;max-width:314px;height:48px;border:1px solid #cdd7e5;border-radius:4px;background-color:#fff;text-decoration:none;font-size:14px;color:#2856ae;background-color:#fcfdff;transition:color 0.5s ease-out}.card__button-microsoft:hover,.card__button-google:hover{cursor:pointer;color:#00308d}.card__button-google::after,.card__button-microsoft::after{left:14px;top:9px;content:'';position:absolute;width:28px;height:28px}.card__button-google::after{background:url("../images/Google__G__Logo.svg")}.card__button-microsoft::after{background:url("../images/Microsoft_logo11.svg")}.card__decoration{position:relative;top:20px;height:1px;max-width:264px;width:100%;background-color:#e2e9f3;border-radius:1px;z-index:1}.card__middle-paragraph{margin-bottom:4px;padding:0 8px;font-size:1.28em;color:#8c93a3;background:#fff;z-index:2}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}*{box-sizing:border-box}html,body,ul,li,button,input,textarea,h1,h2,h3,h4,h5,h6,p{font-family:'Inter', sans-serif;font-size:14px;line-height:19px;margin:0;padding:0}hr{border-width:0}.form{display:flex;flex-direction:column;align-items:center}.form__input{margin-top:16px;padding:10px 18px 10px 8px;width:100%;max-width:264px;height:40px;border:1px solid #cdd7e5;border-radius:4px;font-size:1.14em;color:#2b3151;line-height:19px;background-color:#fcfdff}.form__input:focus{outline:none !important;border:1px solid #61A8EA;background-color:#f5faff;color:#2b3151}.form__input.error{border:1px solid #e1485b !important;background-color:#fffbfb}.form__input--password{margin-top:24px}.form__forgot-password{margin:24px 0 26px 0;font-size:1.14em;line-height:1.36em;color:#2856ae;border-style:none;background-color:#fff}.form__forgot-password:hover{cursor:pointer;color:#00308d}.form__input-group{align-self:flex-start}.form__label{font-size:1.14em;line-height:1em;color:#2b3151;display:flex;justify-content:center;align-items:center}.form__label:hover{cursor:pointer}.form__checkmark{width:20px;height:20px;margin-right:8px}.form__container{display:inline-block;flex-shrink:0;flex-grow:0;border:2px solid #b0baca;border-radius:0.25em;background-repeat:no-repeat;background-position:center center;background-size:130%;height:20px;width:20px;margin-right:8px}.form__icon{position:relative;left:-2px;top:-2px;display:none;width:20px;height:20px;background-color:#3ebf8a;border-radius:0.25em}.form__checkmark:checked+label .form__icon{display:block}.form__button{margin:25px 0 0;width:100%;max-width:264px;height:48px;font-weight:500;font-size:1.3em;line-height:1.26em;background:#3ebf8a;color:#fff;border-style:none;border-radius:3px;transition:background-color 1s ease-out}.form__button:hover{cursor:pointer;background-color:#008960}.form__error{width:100%;margin-top:2px;text-align:end;font-size:12px;line-height:15px;color:#e1485b;margin-bottom:-16px}.message{color:green;margin-bottom:-20px}.signIn__paragraph{margin:16px 0 -8px;line-height:1.44em;font-size:1em;text-align:center;padding:0 16px;color:#2b3151}.signIn__link{text-decoration:none;color:#2856ae}.restore-password__button-wrapper{margin-top:24px}
